<script>
//Structures
import contentLoading from "@/components/app-structures/app-content-loading.vue";
import toolbar from "@/components/app-structures/app-navbar-toolbar-tank.vue";
import popupAdd from "@/views/Applications/Tank/tank-info-add.vue";
import { errorMonitor } from "events";
import moment from "moment";

//API
import axios from "/axios.js";

//DataGrid
import {
  DxDataGrid,
  DxSearchPanel,
  DxPaging,
  DxPager,
  DxScrolling,
  DxColumn,
  DxExport,
  // DxToolbar,
  // DxItem,
  // DxEditing,
  DxLookup,
  // DxButton,
  DxHeaderFilter,
  DxFilterRow,
  DxSelection
} from "devextreme-vue/data-grid";

export default {
  name: "TankList",
  components: {
    toolbar,
    contentLoading,
    popupAdd,
    DxDataGrid,
    DxSearchPanel,
    DxPaging,
    DxPager,
    DxScrolling,
    DxColumn,
    DxExport,
    // DxToolbar,
    // DxItem,
    DxHeaderFilter,
    DxFilterRow,
    DxSelection,
    DxLookup
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_INAPP", {
      name: "Storage Tank Management",
      icon: "/img/icon_menu/equipment/Tank.png"
    });

    this.FETCH_SITE();
    this.FETCH_LIST();
  },
  data() {
    return {
      isAdd: false,
      isEdit: false,
      isLoading: false,
      editInfo: "",
      infoClient: {},
      tankList: [],
      tankListFiltered: [],
      dataGridAttributes: {
        class: "data-grid-style"
      },
      search_key: null,
      siteList: []
    };
  },
  computed: {},
  watch: {
    // whenever search_key change, this function will run
    search_key() {
      this.SEARCH_GET(this.search_key);
    }
  },
  methods: {
    format_inservice_date(item) {
      if (item.inservice_date) {
        return moment(item.inservice_date).format("LL");
      }
      return "N/A";
    },
    VIEW_INFO(e) {
      console.log(e.selectedRowKeys);
      if (e.selectedRowKeys != null) {
        this.$router.push("/tank/tag/" + e.selectedRowKeys + "/info");
      }
    },
    TOGGLE_POPUPADD() {
      this.isAdd = !this.isAdd;
    },
    FETCH_SITE() {
      axios({
        method: "get",
        url: "/master-data/get-md-site-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          console.log(res);
          if (res.status == 200 && res.data) {
            this.siteList = res.data;
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            error.code +
              " " +
              error.response.status +
              " " +
              errorMonitor.message
          );
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_LIST() {
      this.isLoading = true;
      axios({
        method: "get",
        url: "TankInfo",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          // console.log(res);
          if (res.status == 200 && res.data) {
            this.tankList = res.data;
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            error.code +
              " " +
              error.response.status +
              " " +
              errorMonitor.message
          );
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    SEARCH_CLEAR() {
      this.search_key = null;
    },
    SEARCH_GET(searchValue) {
      let tankListFiltered = this.tankList;

      if (searchValue != "" && searchValue) {
        tankListFiltered = tankListFiltered.filter(item => {
          return item.line_no.toUpperCase().includes(searchValue.toUpperCase());
        });
      }
      this.tankListFiltered = tankListFiltered;
    }
  }
};
</script>

